const completeSvg = '<svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 22 22" style="enable-background:new 0 0 22 22;" xml:space="preserve"><rect y="0" class="noFill" width="22" height="22"/><g><path class="fill" d="M9.7,14.4L9.7,14.4c-0.2,0-0.4-0.1-0.5-0.2l-2.7-2.7c-0.3-0.3-0.3-0.8,0-1.1s0.8-0.3,1.1,0l2.1,2.1l4.8-4.8c0.3-0.3,0.8-0.3,1.1,0s0.3,0.8,0,1.1l-5.3,5.3C10.1,14.3,9.9,14.4,9.7,14.4z"/></g></svg>';

const addBtn = document.querySelector('#addItem'); // add button
const item = document.getElementById('item');  // input tag

const todo_listGroup = document.querySelector('#todo') // ul element
const completed_listGroup = document.querySelector('#completed');

const data = (localStorage.getItem('todo-list')) ? JSON.parse(localStorage.getItem('todo-list')) :
 {
  todo: [],
  completed: []
};

renderTodolist();
// console.log(data);
// console.log(JSON.parse(localStorage.getItem('todo-list')));

addBtn.addEventListener('click', ()=>{
  let value = item.value;
  // console.log(value);
  if(value) {
    data.todo.push(value);
    addItem(value);
    item.value = '';
    // console.log(data);
    dataUpdate();
  }
})



item.addEventListener('keyup', (e)=> {
  let value = e.target.value;
  // console.log(value);
  
  if(e.code === "Enter" && value) {
     data.todo.push(value);
    addItem(value);
    item.value = '';
  }
  dataUpdate();
} )

// render todo list function 
function renderTodolist(){
  if(!data.todo.length && !data.completed.length) return;

  for(let i = 0; i < data.todo.length; i++) {
    let listItem = data.todo[i];
    addItem(listItem);
  }

  for(let j = 0; j < data.completed.length; j++) {
    let listItem = data.completed[j];
    addItem(listItem, true);
  }
}

// localStorage data저장소 함수 
function dataUpdate () {
  // console.log(data);
  localStorage.setItem('todo-list', JSON.stringify(data));

}



// console.log(removeSvg);
// remove Item;
function removeItem(){
  let deleteItem = this.parentNode.parentNode
  let parent = deleteItem.parentNode;
  console.log(deleteItem.parentNode)
  parent.removeChild(deleteItem);
  
  // console.log(parent.id)
  let id = parent.id
  let deleteText = deleteItem.innerText;
  if(id === 'todo') {
    data.todo.splice(data.todo.indexOf(deleteText), 1);

  } else {
    data.completed.splice(data.completed.indexOf(deleteText), 1);
  }
  dataUpdate()
}

// complete Item
function completeItem() {
  let completedList = this.parentNode.parentNode
  
  let completedText = completedList.innerText
  let parent = completedList.parentNode
  let id = parent.id;
  let target = (id === 'todo') ? completed_listGroup : todo_listGroup;

  parent.removeChild(completedList);
  target.insertBefore(completedList, target.childNodes[0])

  if(id === 'todo') {
    data.todo.splice(data.todo.indexOf(completedText), 1)
    data.completed.push(completedText)
  } else {
    data.completed.splice(data.completed.indexOf(completedText), 1)
  data.todo.push(completedText)
  }
  dataUpdate();
  
  
}

// add item;
function addItem(text, completed) {

   let list = (completed) ? completed_listGroup : todo_listGroup; 
   let li = document.createElement('li');
   li.innerText = text;

   var buttons = document.createElement('div');
   buttons.classList.add('buttons');

   var remove = document.createElement('button');
   remove.classList.add('remove');
   remove.innerHTML = removeSvg;
  // console.log(removeSvg);
   remove.addEventListener('click', removeItem);

   var complete = document.createElement('button');
   complete.classList.add('complete');
   complete.innerHTML = completeSvg;
   complete.addEventListener('click', completeItem)

   buttons.appendChild(remove);
   buttons.appendChild(complete);

   li.appendChild(buttons);
   list.insertBefore(li, list.childNodes[0]);

}

